relative;\n  width: 100%;\n  height: 100%;\n\n  border: solid 1px $fc-border-color;\n\n  line-height: 1.2;\n  font-size: 13px;\n  font-family: \"Helvetica Neue\", Helvetica, \"PingFang SC\", \"Hiragino Sans GB\", \"Microsoft YaHei\", \"\", Arial, sans-serif;\n  user-select: none;\n  overflow: hidden;\n\n  ::-webkit-scrollbar {\n    width: 4px;\n    height: 4px\n  }\n\n  ::-webkit-scrollbar-thumb {\n    background-color: rgba(50,50,50,.3)\n  }\n\n  ::-webkit-scrollbar-track {\n    background-color: rgba(50,50,50,.08)\n  }\n\n  &,\n  * {\n    box-sizing: border-box;\n  }\n\n  &:hover {\n    .fc-controls {\n      opacity: 1;\n      pointer-events: all;\n    }\n  }\n\n  .fc-toolbox + .fc-controls {\n    margin-top: 40px;\n  }\n\n}\n\n.flow-chart {\n  position: relative;\n  width: 100%;\n  min-height: 100%;\n\n  overflow: visible;\n\n  .jtk-endpoint {\n    visibility: hidden;\n  }\n\n  &.fc-endpoint-visible {\n    .jtk-endpoint {\n      visibility: visible;\n    }\n  }\n\n  .fc-node {\n    position: absolute;\n\n    width: $fc-node-base-width;\n\n    color: #666;\n    background-color: #cce5ff;\n\n    border: solid $fc-node-base-border-width #99ccff;\n    border-radius: 4px;\n\n    cursor: default;\n  }\n\n  .fc-node-inner {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n\n    width: 100%;\n    min-height: $fc-node-base-height;\n  }\n\n  .fc-node-text {\n    padding: 2px 4px;\n    text-align: center;\n    word-break: break-all;\n  }\n\n  .fc-node-circle {\n    width: 84px;\n    border-color: #ffe6cc;\n    background-color: #ffffcc;\n    border-radius: 24px;\n  }\n\n  .fc-node-diamond {\n    width: 88px;\n    height: 88px;\n\n    border: none 0;\n    background-color:  transparent; // rgba(0,0,0,0.2);\n\n    .fc-node-inner {\n      padding: 0;\n      width: 64px;\n      height: 64px;\n\n      border: solid $fc-node-base-border-width #ffcc99;\n      background-color: #ffe6cc;\n\n      border-radius: 4px;\n      transform: translate(12px, 12px) rotate(45deg);\n    }\n\n    .fc-node-text {\n      transform: rotate(-45deg);\n    }\n  }\n\n  .fc-node-skeleton {\n    z-index: $z-index-skeleton;\n    display: none;\n\n    position: absolute;\n    left: -$fc-node-skeleton-padding;\n    right: -$fc-node-skeleton-padding;\n    top: -$fc-node-skeleton-padding;\n    bottom: -$fc-node-skeleton-padding;\n\n    padding: $fc-node-skeleton-padding;\n\n    opacity: 0.5;\n\n    pointer-events: none;\n  }\n\n  .fc-selected {\n    &.fc-node {\n      .fc-node-skeleton {\n        display: block;\n      }\n    }\n\n    &.jtk-connector {\n      path {\n        &:first-of-type {\n          stroke: $color-primary;\n        }\n\n        &.jtk-overlay {\n          fill: $color-primary;\n          stroke: $color-primary;\n        }\n      }\n    }\n  }\n\n  .fc-disabled {\n    &.fc-node {\n      filter: grayscale(100%);\n      opacity: 0.5;\n    }\n  }\n\n  .fc-node-text-input {\n    width: 100%;\n  }\n\n  [contenteditable='true'] {\n    caret-color: red;\n\n    &:focus {\n      outline: inset 1px transparent;\n      padding: 2px 4px;\n    }\n  }\n\n  .jtk-overlay {\n    z-index: $z-index-jtk-overlay;\n    font-size: 12px;\n    color: #aab7c4;\n    background: #fff;\n    pointer-events: none;\n  }\n}\n","@import \"vars\";\n\n// $ant-size \n// $ant-width \n// $speed \n// $keyframe-id  id\n@mixin marching-ants-init($ant-size, $ant-width, $speed, $keyframe-id){\n  background-size:\n    $ant-size $ant-width,\n    $ant-size $ant-width,\n    $ant-width $ant-size,\n    $ant-width $ant-size;\n  background-position:\n    0 0,\n    0 100%,\n    0 0,\n    100% 0;\n  background-repeat:\n    repeat-x,\n    repeat-x,\n    repeat-y,\n    repeat-y;\n  animation: marching-ants-#{$keyframe-id} $speed;\n  animation-timing-function: linear;\n  animation-iteration-count: infinite;\n  animation-play-state: paused;\n\n  &:hover,\n  &.marching {\n    animation-play-state: running;\n  }\n\n  &.reverse {\n    animation-direction: reverse;\n  }\n};\n\n// $color1 \n// $color2 \n@mixin marching-ants-color($color1, $color2){\n  background-image:\n    linear-gradient(to right, $color1 50%, $color2 50%),\n    linear-gradient(to right, $color1 50%, $color2 50%),\n    linear-gradient(to bottom, $color1 50%, $color2 50%),\n    linear-gradient(to bottom, $color1 50%, $color2 50%);\n};\n\n// $ant-size \n@mixin marching-ants-animation($ant-size, $keyframe-id){\n  @keyframes marching-ants-#{$keyframe-id} {\n    0% {\n      background-position:\n        0 0,\n        0 100%,\n        0 0,\n        100% 0;\n    }\n    100% {\n      background-position:\n        2*$ant-size 0,\n        -2*$ant-size 100%,\n        0 -2*$ant-size,\n        100% 2*$ant-size;\n    }\n  }\n}\n\n@include marching-ants-animation($ant-size, 1);\n\n.marching-ants {\n  @include marching-ants-init($ant-size, 1px, 2s, 1);\n  @include marching-ants-color($color-primary, transparent);\n}\n\n\n\n\n\n\n\n","@import \"vars\";\n@import \"march-ants\";\n\n// \n.jtk-connector {\n\n  // hover \n  &.jtk-hover {\n    path {\n      stroke: $color-primary;\n    }\n  }\n}\n\n// \n.jtk-endpoint {\n  &:hover {\n    circle {\n      fill: $color-primary;\n    }\n  }\n\n  // \n  &.jtk-dragging {\n    circle {\n      fill: $color-primary;\n    }\n  }\n\n  // \n  &.jtk-endpoint-drop-allowed {\n    circle {\n      fill: $color-primary;\n    }\n  }\n\n}\n","$color-primary: #026dff;\n$fc-border-color: #dfe2e5;\n\n$fc-node-base-width: 128px;\n$fc-node-base-height: 44px;\n$fc-node-base-border-width: 1px;\n\n$fc-node-skeleton-padding: 4px;\n\n$ant-size: 8px;\n\n$z-index-skeleton: -1;\n$z-index-toolbox: 99;\n$z-index-controls: 100;\n$z-index-line-ball: 101;\n$z-index-jtk-overlay: 99;\n\n"],"sourceRoot":""}]);
